// --- Main Tool Component ---

const ToolApp: React.FC<ToolAppProps> = ({ user, org, permissions, logAction }) => {
  // Log received props, especially logAction
  console.log('ToolApp Props Received:', { user, orgId: org?.id, permissions, logActionExists: !!logAction });
  console.log('ToolApp using imported db:', db); // Log the imported db instance
  console.log('ToolApp using imported auth:', auth); // Log the imported auth instance

  // --- Use the custom hook for item data and filtering --- 
  const {
    items, // Use 'items' which represents the filtered list
    allItems, // Keep allItems for finding items by ID in actions
    loading,
    error,
    searchTerm,
    currentFilter,
    showFlaggedOnly,
    setSearchTerm,
    setCurrentFilter,
    setShowFlaggedOnly,
    setError // Use the setError from the hook
  } = useStockItems(org?.id);

  // Log hook return values for debugging
  console.log('[IncomingTool] useStockItems returned:', { loading, error, items_length: items?.length, allItems_length: allItems?.length, currentFilter, showFlaggedOnly, searchTerm });

  // --- END hook usage ---
  
  // --- State Variables REMOVED (now managed by useStockItems) ---
  // const [allItems, setAllItems] = useState<StockItem[]>([]);
  // const [loading, setLoading] = useState<boolean>(true);
  // const [error, setError] = useState<string | null>(null);
  // const [searchTerm, setSearchTerm] = useState<string>('');
  // const [currentFilter, setCurrentFilter] = useState<ItemStatus | 'All'>('All');
  // const [showFlaggedOnly, setShowFlaggedOnly] = useState<boolean>(false);
  // --- END REMOVED state ---
  
  // --- State Variables remaining in ToolApp ---
  const [selectedItemDetails, setSelectedItemDetails] = useState<StockItem | null>(null); 
  const [isAddItemModalOpen, setIsAddItemModalOpen] = useState<boolean>(false); 
  const [editingItem, setEditingItem] = useState<StockItem | null>(null); 
  const [reportingIssueItem, setReportingIssueItem] = useState<StockItem | null>(null);
  const [addingUpdateItem, setAddingUpdateItem] = useState<StockItem | null>(null);
  const [resolvingIssueItem, setResolvingIssueItem] = useState<StockItem | null>(null);
  const [isSettingsModalOpen, setIsSettingsModalOpen] = useState(false); 
  const [itemForNote, setItemForNote] = useState<StockItem | null>(null); 
  const [isUserActivityViewOpen, setIsUserActivityViewOpen] = useState<boolean>(false); 
  const [visibleColumnIds, setVisibleColumnIds] = useState<Array<ColumnConfig['id']>>(() => {
    try {
      const stored = localStorage.getItem(LOCAL_STORAGE_KEY_VISIBLE_COLUMNS);
      const parsed = stored ? JSON.parse(stored) : DEFAULT_VISIBLE_COLUMN_IDS;
      return Array.isArray(parsed) ? parsed : DEFAULT_VISIBLE_COLUMN_IDS;
    } catch (error) {
      console.error("Error reading visible columns from localStorage", error);
      return DEFAULT_VISIBLE_COLUMN_IDS;
    }
  });
  // Action Menu State
  const [openMenuId, setOpenMenuId] = useState<string | null>(null);
  const [menuAnchorEl, setMenuAnchorEl] = useState<null | HTMLElement>(null);
  // --- END remaining state ---

  // Save visible columns to localStorage effect
  useEffect(() => {
    localStorage.setItem(LOCAL_STORAGE_KEY_VISIBLE_COLUMNS, JSON.stringify(visibleColumnIds));
  }, [visibleColumnIds]);

  // Derive visible columns config
  const visibleColumns = visibleColumnIds
      .map(id => ALL_COLUMNS.find(col => col.id === id))
      .filter((col): col is ColumnConfig => col !== undefined); 

  // --- Filtering Logic REMOVED (now in useStockItems) ---

  // Centralized function to update item - uses `allItems` from hook now
  const updateItemAndLog = useCallback(async (itemId: string, userId: string | undefined, changes: Partial<StockItem>, event: ActivityEvent) => {
    console.log('[IncomingTool] updateItemAndLog: Checking db and org.id:', { db_instance: db, org_id: org?.id });
    if (!db || !org?.id || !userId) {
      console.error('Cannot update item: Missing configuration.', { db: !!db, orgId: org?.id, userId });
      setError('Cannot update item: Missing configuration.'); // Use setError from hook
      return;
    }
    console.log(`[IncomingTool] updateItemAndLog: Preparing to update item ${itemId} in org ${org.id}`);
    const itemRef = doc(db, 'orgs', org.id, 'stockItems', itemId);
    const itemBefore = allItems.find(i => i.id === itemId); // Find in allItems from hook
    if (!itemBefore) {
        console.error(`Item ${itemId} not found for update`);
        setError(`Item ${itemId} not found.`);
        return;
    }

    // Combine updates with new activity event and timestamp
    const finalUpdates = removeUndefinedFields({
      ...changes,
      lastUpdated: serverTimestamp(),
    });

    const itemAfter = { ...itemBefore, ...finalUpdates }; // Approximate state after update

    try {
      await updateDoc(itemRef, finalUpdates);
      console.log(`Item ${itemId} updated successfully.`);

      // Log the generic update action
      console.log('updateItemAndLog: Attempting to log action. logAction available?', !!logAction);
      if (logAction) {
        // Determine standardized action name
        let actionName: string;
        try {
          if (event.type === 'EDITED') {
            actionName = 'item.update.fields';
          } else if (event.type === 'FLAG_TOGGLED') {
            actionName = 'item.update.flag';
          } else if (event.type === 'STATUS_CHANGED') {
            const newStatus = event.details?.newStatus?.toLowerCase() || 'unknown';
            actionName = `item.update.status.${newStatus}`;
          } else if (event.type === 'ISSUE_REPORTED') {
            actionName = 'item.update.issue.report';
          } else if (event.type === 'ISSUE_RESOLVED') {
            actionName = 'item.update.issue.resolve';
          } else if (event.type === 'ISSUE_UPDATE_ADDED') {
            actionName = 'item.update.issue.update';
          } else if (event.type === 'NOTE_ADDED') {
            actionName = 'item.add_note';
          } else {
            actionName = event.type; // Fallback
          }
        } catch (nameError) {
          console.error('Error deriving action name for logging:', nameError);
          actionName = event.type;
        }

        // Construct the data payload ensuring types match LogActionData
        const logData: LogActionData = {
           orgId: org.id,
           toolKey: 'incomingtool',
           action: actionName, // actionName is now guaranteed to be string
           entityPath: `stockItems/${itemId}`,
           // Conditionally add before/after/details if they exist
           ...(itemBefore !== undefined && { before: itemBefore }),
           ...(itemAfter !== undefined && { after: itemAfter }),
           ...(event.details !== undefined && { details: event.details })
        };

        await logAction(logData); // Pass the correctly typed object

      }

    } catch (e) {
      console.error(`Error updating item ${itemId}:`, e);
      setError(`Failed to update item: ${(e as Error).message}`);
      throw e; // Re-throw error to be caught by calling handler if needed
    }
  }, [org, allItems, logAction, setError]); // Add setError to dependencies

  // Item action handler - uses `allItems` from hook now
  const handleItemAction = useCallback(async (itemId: string, action: ActionDefinition) => {
    console.log(`Action: ${action.label} on item ${itemId}`);
    const item = allItems.find(i => i.id === itemId); // Find in allItems from hook
    if (!item) return;

    switch (action.id) {
      case 'Edit Item':
        setEditingItem(item);
        setIsAddItemModalOpen(true);
        break;
      case 'Flag Item':
      case 'Unflag Item':
        const newFlagState = action.id === 'Flag Item';
        await updateItemAndLog(itemId, user?.uid, 
          { isFlagged: newFlagState }, 
          createActivityEvent(user?.uid || '', 'FLAG_TOGGLED', { isFlagged: newFlagState })
        );
        break;
      case 'Mark as Delivered':
        if (item.currentStatus !== 'Delivered') {
          await updateItemAndLog(itemId, user?.uid,
            { currentStatus: 'Delivered', dateDelivered: new Date().toISOString() },
            createActivityEvent(user?.uid || '', 'STATUS_CHANGED', { previousStatus: item.currentStatus, newStatus: 'Delivered' })
          );
        }
        break;
      case 'Archive':
         if (item.currentStatus !== 'Archived') {
          await updateItemAndLog(itemId, user?.uid, 
            { currentStatus: 'Archived' },
            createActivityEvent(user?.uid || '', 'STATUS_CHANGED', { previousStatus: item.currentStatus, newStatus: 'Archived' })
          );
         }
        break;
      case 'Add Note': // New case for adding a general note
        setItemForNote(item); // Set the item context for the modal
        break;
      case 'Report Issue': // Close detail view & open report modal
        setSelectedItemDetails(null);
        setReportingIssueItem(item);
        break;
      case 'Resolve Issue': // Close detail view & open resolve modal
        setSelectedItemDetails(null);
        setResolvingIssueItem(item);
        break;
      case 'Add Issue Update': // Close detail view & open add-note modal
        setSelectedItemDetails(null);
        setAddingUpdateItem(item);
        break;
      case 'View Details': // Open item detail modal
        setSelectedItemDetails(item);
        break;
      // Default case for actions handled elsewhere or needing no state change here (like View Details)
    }
  }, [user?.uid, allItems, updateItemAndLog, setEditingItem, setIsAddItemModalOpen, setReportingIssueItem, setAddingUpdateItem, setResolvingIssueItem, setSelectedItemDetails]);

  // --- RESTORED MODAL HANDLERS ---
  const handleSaveItem = async (newItemOrUpdate: Omit<StockItem, 'id' | 'activityLog' | 'lastUpdated' | 'currentStatus' | 'dateDelivered'>, itemId?: string) => { 
    if (!user?.uid || !org?.id || !db) {
      setError('Missing user, org, or DB connection.');
      return;
    }

    // We need to handle potential missing fields if editing vs adding
    // This might require more complex logic or adjusting AddItemForm
    // For now, assume newItemOrUpdate contains necessary fields, add placeholders for required ones if adding
    const dataToSave = removeUndefinedFields({
      ...newItemOrUpdate,
      lastUpdated: serverTimestamp(),
    });

    try {
      if (itemId) { // Editing existing item
        console.log(`[IncomingTool] handleSaveItem: Updating item ${itemId}`);
        const itemRef = doc(db, 'orgs', org.id, 'stockItems', itemId);
        // Use correct field name: changedFields
        const event = createActivityEvent(user.uid, 'EDITED', { changedFields: Object.keys(newItemOrUpdate) });
        // Pass the potentially incomplete data - ensure updateItemAndLog handles partial updates
        await updateItemAndLog(itemId, user.uid, dataToSave, event); 
      } else { // Adding new item
        console.log('[IncomingTool] handleSaveItem: Adding new item');
        const collectionRef = collection(db, 'orgs', org.id, 'stockItems');
        // Add required fields not expected from AddItemForm's type inference
        const completeNewItemData = {
          ...dataToSave,
          currentStatus: 'Pending Delivery' as ItemStatus, // Default status
          activityLog: [createActivityEvent(user.uid, 'CREATED')],
          orgId: org.id,
        };
        const docRef = await addDoc(collectionRef, completeNewItemData);
        console.log('[IncomingTool] handleSaveItem: Item added with ID:', docRef.id);
        if (logAction) {
          await logAction({
            orgId: org.id,
            toolKey: 'incomingtool',
            action: 'item.create',
            entityPath: `stockItems/${docRef.id}`,
            after: { id: docRef.id, ...completeNewItemData }
          });
        }
      }
      setIsAddItemModalOpen(false);
      setEditingItem(null);
    } catch (e) {
      console.error("Error saving item:", e);
      setError(`Failed to save item: ${(e as Error).message}`);
    }
  };

  const handleSaveReportIssue = async (itemId: string, issueDescValue: string, userId: string) => { // Renamed param
    // Use correct field name: issueDescription
    const event = createActivityEvent(userId, 'ISSUE_REPORTED', { issueDescription: issueDescValue });
    await updateItemAndLog(itemId, userId, 
      { currentStatus: 'Issue', issueDescription: issueDescValue }, // Save the description
      event
    );
    setReportingIssueItem(null); // Close modal
  };

  const handleSaveIssueUpdate = async (itemId: string, note: string, userId: string) => {
    const event = createActivityEvent(userId, 'ISSUE_UPDATE_ADDED', { note }); // 'note' is correct
    await updateItemAndLog(itemId, userId, 
        { /* No direct field changes */ }, 
        event
    );
    console.warn("Issue update note logged, but not stored directly on item field in this basic implementation.");
    setAddingUpdateItem(null); // Close modal
  };

  const handleSaveResolveIssue = async (itemId: string, resolutionNoteValue: string, userId: string) => { 
    const item = allItems.find(i => i.id === itemId);
    if (!item) return;
    const resolvedStatus: ItemStatus = 'Delivered'; // Simple assumption
    // Use correct field name: resolutionOutcome
    const event = createActivityEvent(userId, 'ISSUE_RESOLVED', { resolutionOutcome: resolutionNoteValue });
    await updateItemAndLog(itemId, userId, 
      { currentStatus: resolvedStatus, issueDescription: 'Resolved: ' + resolutionNoteValue }, 
      event
    );
    setResolvingIssueItem(null); // Close modal
  };

  const handleSaveNote = async (itemId: string, note: string, userId: string) => {
    const event = createActivityEvent(userId, 'NOTE_ADDED', { note }); // 'note' is correct
     await updateItemAndLog(itemId, userId, 
        { /* No direct field changes */ }, 
        event
     );
     console.warn("General note logged, but not stored directly on item field.");
    setItemForNote(null); // Close modal
  };
  // --- END RESTORED MODAL HANDLERS ---

  // Other handlers (handleSaveReportIssue, handleSaveIssueUpdate, etc.) - use `allItems` from hook if needed
  // ... other handlers ...

  const handleViewDetails = useCallback((itemId: string) => {
    const item = allItems.find(i => i.id === itemId); // Find in allItems from hook
    if (!item) {
      console.error("Item not found for viewing details:", itemId);
      setError('Item not found.'); // Use setError state setter
      return;
    }
    console.log("Viewing details for:", item);
    if (!org) {
      console.error("Org ID is missing, cannot fetch logs.");
      setError('Organization context is missing.'); // Use setError state setter
      return;
    }
    setSelectedItemDetails(item); // Use setSelectedItemDetails state setter
    // TODO: Focus management - consider focusing the modal or close button
  }, [org, allItems, setError]); // Add setError if used

  // --- Handler for opening the action menu ---
  const handleMenuOpen = (event: React.MouseEvent<HTMLElement>, item: StockItem) => { // Modified to accept 'item'
    setMenuAnchorEl(event.currentTarget);
    setOpenMenuId(item.id); // Use item.id here
  };

  // --- RENDER LOGIC --- 

  // Draggable sensors setup
  const sensors = useSensors(/* ... */);

  // Mobile check
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  // --- Mobile Item Card Component (Restored) ---
  interface MobileItemCardProps {
    item: StockItem;
    onViewDetails: (itemId: string) => void;
    onActionClick: (event: React.MouseEvent<HTMLElement>, item: StockItem) => void;
  }

  const MobileItemCard: React.FC<MobileItemCardProps> = ({ item, onViewDetails, onActionClick }) => {
    // Display key info vertically, make card clickable for details, icon for actions
    return (
      <Card sx={{ mb: 2, cursor: 'pointer' }} onClick={() => onViewDetails(item.id)}> 
        <CardContent sx={{ pb: '8px !important' }}> 
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
            {/* Left side: Main info */}
            <Box sx={{ flexGrow: 1, mr: 1 }}>
              <Typography variant="subtitle1" component="div" sx={{ fontWeight: 'bold', lineHeight: 1.3 }}>
                {item.productName || item.deliveryName || 'Unnamed Item'}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Status: {item.currentStatus}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Qty: {item.quantity ?? 'N/A'} | Seller: {item.seller || 'N/A'}
              </Typography>
              <Typography variant="body2" color="text.secondary" sx={{ fontSize: '0.8rem' }}>
                Order Date: {item.orderDate ? new Date(item.orderDate).toLocaleDateString() : 'N/A'}
              </Typography>
            </Box>
            {/* Right side: Action Menu Button */}
             <IconButton size="small" onClick={(e) => {
               e.stopPropagation(); // Prevent card click
               onActionClick(e, item); // Open action menu
             }} sx={{ mt: -1, mr: -1 }}> 
               <MoreVertIcon />
             </IconButton>
           </Box>
        </CardContent>
      </Card>
    );
  };
  // --- END Mobile Item Card Component ---

  // renderActionMenu - uses `allItems` from hook
  const renderActionMenu = () => {
    if (!openMenuId) return null;
    const item = allItems.find(i => i.id === openMenuId); // Find in allItems from hook
    // ... rest of renderActionMenu ...
  };

  // Main return statement
  return (
    <Box sx={{ p: isMobile ? 1 : 2 }}> {/* Add padding to the main container */}
      {/* --- RESTORED Top Bar --- */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
        <Typography variant="h6">Stock Items</Typography>
        <Box>
          <Tooltip title="Add New Item">
            <Button
              variant="contained"
              startIcon={<AddIcon />}
              onClick={() => {
                  setEditingItem(null); // Ensure we are adding, not editing
                  setIsAddItemModalOpen(true);
              }}
              sx={{ mr: 1 }}
            >
              Add Item
            </Button>
          </Tooltip>
          <Tooltip title="Table Settings">
            <IconButton onClick={() => setIsSettingsModalOpen(true)} sx={{ mr: 1 }}>
              <Settings />
            </IconButton>
          </Tooltip>
          <Tooltip title="View User Activity">
            <IconButton onClick={() => setIsUserActivityViewOpen(true)}>
              <HistoryIcon />
            </IconButton>
          </Tooltip>
        </Box>
      </Box>
      {/* --- END RESTORED Top Bar --- */}
      
      {/* Error/Loading Messages - use `error` and `loading` from hook */}
      {error && <Alert severity="error" onClose={() => setError(null)} sx={{ /* ... */ }}>{error}</Alert>} 
      {loading && <LinearProgress sx={{ position: 'absolute', top: 0, left: 0, right: 0 }} />} 
      
      {/* Search and Flag Toggle Controls - use state/setters from hook */}
      <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 2 }}>
         {/* Search TextField - Give it flexGrow */}
         <TextField
           label="Search Items"
           variant="outlined"
           size="small"
           fullWidth // Ensure it tries to take full width within its container
           sx={{ flexGrow: 1 }} // Allow it to grow and take available space
           value={searchTerm} // from hook
           onChange={(e) => setSearchTerm(e.target.value)} // from hook
          />
         {/* Flag Toggle Checkbox */}
         <FormControlLabel 
           control={
             <Checkbox 
               checked={showFlaggedOnly} // from hook
               onChange={(e) => setShowFlaggedOnly(e.target.checked)} // from hook
               size="small" 
             />
           }
           label={<Typography variant="body2">Show Flagged Only</Typography>}
           sx={{ mr: 0 }}
         />
       </Box>

      {/* --- RESTORED Filter Tabs --- */}
      <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 2 }}>
        <Tabs 
          value={currentFilter} // from hook
          onChange={(event, newValue) => setCurrentFilter(newValue)} // from hook
          variant="scrollable"
          scrollButtons="auto"
          allowScrollButtonsMobile 
          aria-label="Stock item status filter"
        >
          <Tab label="All" value="All" />
          <Tab label="Pending" value="Pending Delivery" />
          <Tab label="Delivered" value="Delivered" />
          <Tab label="Issue" value="Issue" />
          <Tab label="Late" value="Late" />
          <Tab label="Archived" value="Archived" />
        </Tabs>
      </Box>
      {/* --- END RESTORED Filter Tabs --- */}

      {/* Stock List Display - uses `items` from hook */}
      <DndContext
        // ... props ...
      >
        {/* Desktop Table View */}
        {!isMobile && (
          <TableContainer component={Paper} sx={{ mt: 2, maxHeight: 'calc(100vh - 250px)', overflowY: 'auto' }}>
            <Table stickyHeader size="small">
              <TableHead>
                <TableRow>
                  {/* Wrap headers in SortableContext for D&D */}
                  <SortableContext items={visibleColumnIds} strategy={horizontalListSortingStrategy}>
                    {visibleColumns.map(col => (
                      <DraggableHeader key={col.id} column={col} />
                    ))}
                  </SortableContext>
                </TableRow>
              </TableHead>
              {/* ENSURE TableBody IS DIRECTLY INSIDE Table */}
              <TableBody>
                {items.map((item, index) => ( // Use items from hook
                  <TableRow
                    key={item.id}
                    hover
                    sx={{ '& > *': { borderBottom: 'unset' }, cursor: 'pointer' }}
                    onClick={() => handleViewDetails(item.id)}
                  >
                    {/* Map visible columns to cells */} 
                    {visibleColumns.map(column => (
                      <TableCell 
                        key={column.id}
                        align={['quantity', 'pricePerItem'].includes(column.id) ? 'right' : 'left'}
                        sx={{ 
                          whiteSpace: 'nowrap', 
                          overflow: 'hidden', 
                          textOverflow: 'ellipsis', 
                          maxWidth: ['productName', 'deliveryName', 'seller'].includes(column.id) ? 200 : undefined, // Limit width for text fields
                           // Conditional styling for Flagged column
                           ...(column.id === 'isFlagged' && { 
                               p: '6px 8px', // Adjust padding
                               textAlign: 'center'
                           }),
                           // Conditional styling for Actions column
                           ...(column.id === 'actions' && { 
                               p: '0px 8px', // Adjust padding for icon
                               textAlign: 'center'
                           }),
                        }}
                      >
                        {column.id === 'actions' ? (
                          <IconButton size="small" onClick={(e) => { e.stopPropagation(); handleMenuOpen(e, item); }}>
                            <MoreVertIcon fontSize="small" />
                          </IconButton>
                        ) : column.id === 'isFlagged' ? (
                            item.isFlagged ? <FlagIcon fontSize="inherit" color="error" /> : null
                        ) : column.id === 'orderDate' || column.id === 'dateDelivered' ? (
                           item[column.id] ? new Date(item[column.id] as string).toLocaleDateString() : ''
                        ) : (
                          item[column.id as keyof StockItem] ?? '' // Use nullish coalescing for undefined/null
                        )}
                      </TableCell>
                    ))}
                  </TableRow>
                ))}
                {/* "No items" row - MUST be inside TableBody */} 
                {items.length === 0 && !loading && ( // Add !loading check
                  <TableRow>
                    <TableCell colSpan={visibleColumns.length} align="center">
                      No items match the current filter.
                    </TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </TableContainer>
        )}
        {/* Mobile Card View */}
        {isMobile && (
          <Box sx={{ p: 1 }}> {/* Add padding around the card list */} 
            {items.map((item) => ( // Use items from hook
              <MobileItemCard 
                key={item.id} 
                item={item} 
                onViewDetails={handleViewDetails}
                onActionClick={handleMenuOpen} // Pass handleMenuOpen here
              />
            ))}
            {items.length === 0 && ( // Use items from hook
               // Restore Typography for mobile view
               <Typography align="center" sx={{ mt: 2, color: 'text.secondary' }}>
                 No items match the current filter.
               </Typography>
             )}
          </Box>
        )}
      </DndContext>
      
      {/* --- RESTORED MODALS --- */}
      {/* Add/Edit Item Modal */}
      <AddItemForm
        isOpen={isAddItemModalOpen}
        onClose={() => setIsAddItemModalOpen(false)}
        onSave={handleSaveItem} 
        initialData={editingItem}
      />

      {/* Item Details Modal */}
      {selectedItemDetails && (
        <ItemDetailModal
          isOpen={!!selectedItemDetails}
          onClose={() => setSelectedItemDetails(null)}
          item={selectedItemDetails}
          orgId={org?.id}
          onActionSelected={handleItemAction}
        />
      )}

      {/* Report Issue Modal */}
      {reportingIssueItem && (
        <ReportIssueModal
          isOpen={!!reportingIssueItem}
          onClose={() => setReportingIssueItem(null)}
          item={reportingIssueItem}
          onSubmit={(description: string) => 
            handleSaveReportIssue(reportingIssueItem.id, description, user?.uid || '')
          }
        />
      )}
      
      {/* Add Issue Update Modal - Rename onSave to onSubmit */}
      {addingUpdateItem && (
         <AddUpdateModal
           isOpen={!!addingUpdateItem}
           onClose={() => setAddingUpdateItem(null)}
           item={addingUpdateItem} // REVERTED to 'item'
           // Pass lambda calling correct handler
           onSubmit={(note: string) => 
            handleSaveIssueUpdate(addingUpdateItem.id, note, user?.uid || '')
           }
           // userId={user?.uid} // REMOVED
         />
      )}

      {/* Resolve Issue Modal - Rename onSave to onSubmit */}
      {resolvingIssueItem && (
          <ResolveIssueModal
            isOpen={!!resolvingIssueItem}
            onClose={() => setResolvingIssueItem(null)}
            item={resolvingIssueItem} // REVERTED to 'item'
            // Pass lambda calling correct handler
            onSubmit={(outcome: string, note?: string) => 
              handleSaveResolveIssue(resolvingIssueItem.id, outcome, user?.uid || '') // Pass item id, outcome as note, user id
            }
            // userId={user?.uid} // REMOVED
          />
      )}

      {/* Add Note Modal (if separate from AddUpdateModal) - Rename onSave to onSubmit */}
      {itemForNote && (
         <AddUpdateModal 
            isOpen={!!itemForNote}
            onClose={() => setItemForNote(null)} 
            item={itemForNote} // REVERTED to 'item'
             // Pass lambda calling correct handler
            onSubmit={(note: string) => 
              handleSaveNote(itemForNote.id, note, user?.uid || '')
            }
            // userId={user?.uid} // REMOVED
            // isNoteOnly={true} // REMOVED - AddUpdateModal doesn't have this prop
         />
      )}

      {/* Table Settings Modal - Fix props */}
      <TableSettingsModal
        isOpen={isSettingsModalOpen}
        onClose={() => setIsSettingsModalOpen(false)}
        allColumns={ALL_COLUMNS.filter(c => c.id !== 'actions')} // Exclude 'actions' column
        initialVisibleColumns={visibleColumns} // RENAMED prop, using derived object array
        onSave={setVisibleColumnIds} // RENAMED prop, handler is correct
      />

      {/* User Activity View Drawer/Modal - Fix props */}
      <UserActivityView
        isOpen={isUserActivityViewOpen}
        db={db}
        orgId={org?.id}
        onClose={() => setIsUserActivityViewOpen(false)}
      />
      {/* --- END RESTORED MODALS --- */}

      {/* Render Action Menu (unchanged) */}
      {renderActionMenu()}
    </Box>
  );
}

export default ToolApp;